<template>
	<view class="container">
		<view class="bg"></view>
		<view class="form">
			<view class="header">
				<view class="title">人工智能生成你描述的场景</view>
			</view>
			<u-transition :show="true" mode="fade-left">
				<view class="panel">
					<view class="head">
						<view class="title">描述您的场景</view>
						<view class="tips">
							<u-button text="清空问题" size="mini" iconColor="#f56c6c" color="#fef0f0" icon="trash"
								:customStyle="{color:'#f56c6c'}" @click="resetQuestion">
							</u-button>
						</view>
					</view>
					<view class="textarea">
						<u--textarea v-model="question" height="140" placeholder="请详细描述您的场景,描述越详尽,得到的图片越精准(支持长按粘贴您的描述哦)"
							confirmType="done" count maxlength="200">
						</u--textarea>
					</view>
					<view class="btn-group">
						<view class="btn get">
							<u-button @click="onSubmitGPT" :loading="gptLoading" iconColor="#ffffff" color="#26B3A0"
								icon="edit-pen" text="生成图片"></u-button>
						</view>
					</view>
				</view>
			</u-transition>
		</view>
		<view class="alert">
			<u-alert title="当前使用人数过多,获取图片可能超过30秒钟" type="error"></u-alert>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				question: '',
				gptLoading: false,
			};
		},
		onShareAppMessage(res) {
			return {
				title: 'AI问答：解锁您问题的答案！',
				path: '/pages/index/index',
			}
		},
		onShareTimeline(res) {
			return {
				title: 'AI问答：解锁您问题的答案！',
				path: '/pages/index/index',
			}
		},
		methods: {
			checkToken() {
				const token = uni.getStorageSync('token');
				if (token == '' || token == undefined) {
					return false
				} else {
					return true
				}
			},
			resetQuestion() {
				this.question = '';
			},
			onSubmitGPT() {
				//检测用户是否登录
				let islogin = this.checkToken();
				if (!islogin) {
					uni.navigateTo({
						url: '/pages/login/index'
					});
					return false;
				}
				this.gptLoading = true;
				//已经登录，则调用后端接口数据
				let postData = {
					prompt: this.question
				};
				this.$http.requestApi('POST', 'chatgpt/text/toImg', postData).then(res => {
					const resCode = res.status;
					this.gptLoading = false;
					if (resCode == 200) {
						uni.navigateTo({
							url: '/pages/detail/index?avg=' + res.data.qaId
						});
					} else {
						uni.showToast({
							title: res.msg,
							duration: 2000
						});
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.share {
		position: fixed;
		right: 40rpx;
		bottom: 120rpx;
		width: 40%;

		.u-button {
			box-shadow: 0rpx 10rpx 10rpx #eee !important;
		}
	}

	.tool {
		width: 90%;
		margin: 15rpx auto;

		.header {
			.title {
				margin-bottom: 30rpx;
			}
		}

		.grid {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;

			.item {
				width: 30%;
				display: flex;
				flex-direction: column;
				align-items: center;
				background-color: #F0FAF8;
				margin: 15rpx 0rpx;
				padding: 30rpx 0rpx;
				border-radius: 10rpx;

				.title {
					color: #1B2B20;
					margin-top: 15rpx;
				}

				.desc {
					color: #67776C;
					font-size: 24rpx;
				}
			}
		}
	}

	.alert {
		width: 90%;
		margin: 30rpx auto;
	}

	.form {
		width: 90%;
		margin: 0 auto;
		z-index: 999;

		.header {
			margin-bottom: 30rpx;

			.title {
				text-align: center;
				color: #fff;
				margin: 30rpx 0rpx;
			}
		}

		.panel {
			padding: 30rpx;
			background-color: #fff;
			border-radius: 15rpx;
			box-shadow: 0rpx 10rpx 10rpx #eee;

			.head {
				display: flex;
				flex-direction: row;
				justify-content: space-between;

				.tips {
					color: #dd6161;
				}
			}

			.textarea {
				margin-top: 30rpx;
			}

			.btn-group {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				margin-top: 30rpx;

				.get {
					width: 100%;
				}
			}
		}
	}

	.bg {
		position: fixed;
		top: 0rpx;
		left: 0rpx;
		width: 100%;
		background-color: #26B3A0;
		min-height: 200rpx;
		border-radius: 0rpx 0rpx 140rpx 140rpx;
		z-index: -1;
	}
</style>
